@import '../../index.scss';
@import '../../styles.scss';

.ResultScreen {
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 0.9;
  background-color: grey;
  top: 0;
  left: 0;
  z-index: 20;
  &.view {
    display: flex;
  }
  &.hide {
    display: none;
  }
}

.ResultPercent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  margin: 0;
  top: 20%;
  font-size: 50px;
}
