@import './index.scss';
@import './styles.scss';

.App {
  text-align: center;
  position: relative;
  min-height: 100vh;
  font-family: 'Rubik', sans-serif;
}

.UpdateSWContainer {
  position: fixed;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
  opacity: 0.9;
  
  top: 87%;
  left: 0;
  z-index: 18;
  &.view {
    display: flex;
  }
  &.hide {
    display: none;
  }
}

.UpdateSWMessage {
  border-radius: 10px;
  height: 80px;
  width: 300px;
  padding: 5px;
  box-shadow: $box-shadow-small;
  display: flex;
  background-color: $secondary-op1;
  opacity: 1;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  font-size: 15px;
  color: white;
}

.UpdateSWButtons {
    border-radius: 10px;
    height: 40px;
    width: 300px;
    display: flex;
    align-items: stretch;
    flex-direction: row;
    justify-content: center;
    margin: 0;
  }

  .UpdateButton {
    text-align: center;
    border-radius: 10px;
    height: 20px;
    width: 100px;
    margin: 20px;
    box-shadow: $box-shadow-small;
    display: flex;
    background-color: grey;
    opacity: 1;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    margin: 0;
    font-size: 15px;
    color: white;
  }
